"use client"
import React, { useEffect, useState } from 'react'
import { z } from 'zod'

const contactSchema = z.object({
  name: z.string().min(2, 'Please enter your name'),
  email: z.string().email('Please enter a valid email'),
  message: z.string().min(10, 'Please include a message'),
  consent: z.boolean().refine(Boolean, 'Consent required'),
  _honeypot: z.string().optional(),
})

type Contact = z.infer<typeof contactSchema>

export default function ContactForm({ defaultSummary = '' }: { defaultSummary?: string }) {
  const [form, setForm] = useState<Contact>({ name: '', email: '', message: defaultSummary || '', consent: false, _honeypot: '' })
  const [errors, setErrors] = useState<Partial<Record<keyof Contact, string>>>({})
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState<string | null>(null)
  const [serverErr, setServerErr] = useState<string | null>(null)

  useEffect(() => {
    if (defaultSummary) setForm((f) => ({ ...f, message: defaultSummary }))
  }, [defaultSummary])

  const update = (k: keyof Contact, v: any) => setForm((s) => ({ ...s, [k]: v }))

  const validate = () => {
    const res = contactSchema.safeParse(form)
    if (!res.success) {
      const zErrors: Partial<Record<keyof Contact, string>> = {}
      res.error.issues.forEach((i) => { const k = i.path[0] as keyof Contact; zErrors[k] = i.message })
      setErrors(zErrors)
      return false
    }
    setErrors({})
    return true
  }

  const submit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!validate()) return
    if (form._honeypot) return // caught bot
    setLoading(true)
    try {
      const res = await fetch('/api/contact', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(form) })
      const data = await res.json()
      if (!res.ok) {
        setServerErr(data.error || 'Server error')
      } else {
        setSuccess(data.requestId)
        setForm({ name: '', email: '', message: '', consent: false, _honeypot: '' })
      }
    } catch (err) {
      setServerErr('Network error')
    } finally { setLoading(false) }
  }

  return (
    <form onSubmit={submit} className="space-y-4">
      <div>
        <label className="block text-sm">Name</label>
        <input aria-label="Name" value={form.name} onChange={(e) => update('name', e.target.value)} className="w-full p-2 mt-1 bg-neutral-900 border border-neutral-700 rounded focus-ring" />
        {errors.name && <div className="text-xs text-red-400">{errors.name}</div>}
      </div>

      <div>
        <label className="block text-sm">Email</label>
        <input aria-label="Email" value={form.email} onChange={(e) => update('email', e.target.value)} className="w-full p-2 mt-1 bg-neutral-900 border border-neutral-700 rounded focus-ring" />
        {errors.email && <div className="text-xs text-red-400">{errors.email}</div>}
      </div>

      <div>
        <label className="block text-sm">Message</label>
        <textarea aria-label="Message" value={form.message} onChange={(e) => update('message', e.target.value)} className="w-full p-2 mt-1 bg-neutral-900 border border-neutral-700 rounded focus-ring" rows={5}></textarea>
        {errors.message && <div className="text-xs text-red-400">{errors.message}</div>}
      </div>

      <div className="hidden">
        <label>Website</label>
        <input name="_honeypot" value={form._honeypot} onChange={(e) => update('_honeypot', e.target.value)} />
      </div>

      <div className="flex items-start gap-2">
        <input id="consent" type="checkbox" checked={form.consent} onChange={(e) => update('consent', e.target.checked)} />
        <label htmlFor="consent" className="text-sm">I consent to the <a className="underline" href="/terms">Terms</a> and <a className="underline" href="/privacy">Privacy</a> policies</label>
      </div>
      {errors.consent && <div className="text-xs text-red-400">{errors.consent}</div>}

      <div>
        <button type="submit" disabled={loading} className="px-4 py-2 rounded bg-neutral-700 focus-ring">{loading ? 'Sending…' : 'Send Message'}</button>
      </div>
      {serverErr && <div className="text-red-400">{serverErr}</div>}
      {success && <div className="text-green-400">Message sent — request ID: {success}</div>}
    </form>
  )
}
